import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {YourVehicleComponent} from './your-vehicle.component';
import {YourVehicleGuard} from './your-vehicle.guard';

/**
 * Adjust module routing here.
 */
const routes: Routes = [
    {
        path: '',
        component: YourVehicleComponent,
        canActivate: [YourVehicleGuard]
    }
];

@NgModule({
    imports: [
        RouterModule.forChild(routes),
    ],
    exports: [RouterModule],
})

export class YourVehicleRoutingModule {
}
