import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";

@Component({
  templateUrl: './your-vehicle.component.html',
  styleUrls: ['./your-vehicle.component.css']
})
/**
 * Blank view.
 */
export class YourVehicleComponent implements OnInit {

  pages: Array<string> = [
    '/your-trip',
    '/your-driver',
    '/your-vehicle',
    '/your-destination'
  ];

  /**
   * Component constructor and DI injection point.
   */
  constructor(private router: Router) {
  }

  /**
   * Called part of the component lifecycle. Best first
   * place to start adding your code.
   */
  ngOnInit() {
  }

  onSwipe(event) {
    if (event.direction === 2) {
      // left
    } else if (event.direction === 4) {
      // right
    } else if (event.direction === 8) {
      // up
      this.router.navigate(['your-driver']);
    } else if (event.direction === 16) {
      // down
      this.router.navigate(['your-destination']);
    }
  }

}
